<template>
  <div>
        <h2 class="text-center">QnA </h2>
        <div class="form-group">
            <label for="writer"></label>
            <input readonly type="text" value=""  class="form-control" id="writer" placeholder=" " v-model="qna_userid">
        </div>
        <div class="form-group">
            <label for="title"></label>
            <input type="text" class="form-control" id="title" placeholder=" " v-model="qna_title">
        </div>
        <div class="form-group">
            <label for="content"></label>
            <textarea type="text" class="form-control" id="contnet" placeholder=" " v-model="qna_content"></textarea>
        </div>
        <div class="text-right">
            <button class="btn btn-primary" @click="dataCheck"></button>
            <router-link to="/QnaList"><button class="btn btn-primary"></button></router-link>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:"QnaWrite"
    , data:function(){
        return{
            qna_userid:""
            , qna_title:""
            , qna_content:""
            , user:[]
        }
    }//data
    ,
    beforeCreate(){
    }
    ,
    created(){
        this.no = this.$route.params.no;
        this.user = this.$session.get("userinfo");   
         this.qna_userid = this.user.id;
    }//created
    ,
    methods:{
        dataCheck() {
            let err = false;
            let msg = '';
            !this.qna_userid && (msg = ' ', err = true);
            !err && !this.qna_title && (msg = ' ', err = true);
            !err && !this.qna_content && (msg = ' ', err = true);
            if (err) alert(msg);
            else this.dataSend();
        }//dataCheck
        , dataSend() {
            axios.post('http://localhost:9999/vue/api/qna'
                        , {qna_userid: this.qna_userid, qna_title: this.qna_title, qna_content: this.qna_content})
            .then(() => {alert(' .');
                        this.$router.push("/qnalist");})
            .catch((error)=>{});
        }//dataSend
    }//methods
}
</script>

